<template>
	<view class="container">
		<u-loading-page :loading="loading" loadingText="loading..."></u-loading-page>
		<u-grid border="true" @click="click">
			<u-grid-item v-for="(baseListItem,baseListIndex) in baseList" :name="baseListItem.id" :key="baseListIndex" >
				<view class="item">
					<u-image :src="baseListItem.fileUrl" width="60" height="60"></u-image>
					</view>
				<text class="grid-text">{{baseListItem.name}}</text>
			</u-grid-item>
		</u-grid>
		<u-toast ref="uToast" />
	</view>
</template>

<script>
	import Api from "@/server/index.js";
	export default {
		data() {
			return {
				baseList: [],
				loading: true
			}
		},
		mounted() {
			this.getData()
		},
		created() {
			
		},
		methods: {
			click(name) {
				this.$refs.uToast.success(name)
				uni.navigateTo({
					url:`/pages/functionintroduction/details/index?id=${name}`
					// url: `/pages/liveoperationaltraining/index`
				})
			},
			getData() {
				var data = {}
				Api.getList(data).then(res => {
					console.log(res, 'res')
					if (res.status === 200) {
						this.baseList = res.data
						this.loading = false
					}
				}).catch(error => {
					console.log(error);
				});
			}
		}
	}
</script>

<style lang="scss">
	.grid-text {
		font-size: 14px;
		color: #909399;
		padding: 10rpx 0 20rpx 0rpx;
		/* #ifndef APP-PLUS */
		box-sizing: border-box;
		/* #endif */
	}
	.container {
		height: 100vh;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.item {
		padding: 10px;
	}
</style>
